import newsPage from './news-page.vue';

import uniFab from '@/components/uni-fab/uni-fab.vue';

// 缓存每页最多
const MAX_CACHE_DATA = 100;
// 缓存页签数量
const MAX_CACHE_PAGE = 3;
const TAB_PRELOAD_OFFSET = 1;

export default {
	components: {
		newsPage,uniFab
	},
	data() {
		return {
			tabList: [{
				id: "tab01",
				name: '川菜',
				newsid: 0
			}, {
				id: "tab02",
				name: '鲁菜',
				newsid: 23
			}, {
				id: "tab03",
				name: '粤菜',
				newsid: 223
			}, {
				id: "tab04",
				name: '苏菜',
				newsid: 221
			}, {
				id: "tab05",
				name: '浙菜',
				newsid: 225
			}, {
				id: "tab06",
				name: '闽菜',
				newsid: 208
			}, {
				id: "tab07",
				name: '湘菜',
				newsid: 208
			}, {
				id: "tab08",
				name: '徽菜',
				newsid: 208
			}],
			tabIndex: 0,
			cacheTab: [],
			scrollInto: "",
			navigateFlag: false,
			indicatorLineLeft: 0,
			indicatorLineWidth: 0,
			isTap: false
		}
	},
	 onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
	      /*  console.log("微信信息:"+option.userInfo); //打印出上个页面传递的参数。。 */	  
		  uni.showToast({
		      title:  "这里是"+option.name,
		      duration: 2000,
			  icon:"none",
		  });	  

	},
	onReady() {
		this._lastTabIndex = 0;
		this.swiperWidth = 0;
		this.tabbarWidth = 0;
		this.tabListSize = {};
		this._touchTabIndex = 0;
		this.isIos = uni.getSystemInfoSync().platform.toLowerCase() === 'ios';


		this.pageList = this.$refs.page;







		this.switchTab(this.tabIndex);

		this.getTabbarItemsSize();
	},
	methods: {
		ontabtap(e) {
			console.log("点击了tap页")
			
	

			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			//let offsetIndex = this._touchTabIndex = Math.abs(index - this._lastTabIndex) > 1;


			this.isTap = true;
			var currentSize = this.tabListSize[index];
			this.updateIndicator(currentSize.left, currentSize.width);
			this._touchTabIndex = index;


			this.switchTab(index);
		},
		onswiperchange(e) {
			// 注意：百度小程序会触发2次





		},
		onswiperscroll(e) {
			if (this.isTap) {
				return;
			}

			var offsetX = e.detail.dx;
			var preloadIndex = this._lastTabIndex;
			if (offsetX > TAB_PRELOAD_OFFSET) {
				preloadIndex++;
			} else if (offsetX < -TAB_PRELOAD_OFFSET) {
				preloadIndex--;
			}
			if (preloadIndex === this._lastTabIndex || preloadIndex < 0 || preloadIndex > this.pageList.length - 1) {
				return;
			}
			if (this.pageList[preloadIndex].dataList.length === 0) {
				this.loadTabData(preloadIndex);
			}


			var percentage = Math.abs(this.swiperWidth / offsetX);
			var currentSize = this.tabListSize[this._lastTabIndex];
			var preloadSize = this.tabListSize[preloadIndex];
			var lineL = currentSize.left + (preloadSize.left - currentSize.left) / percentage;
			var lineW = currentSize.width + (preloadSize.width - currentSize.width) / percentage;
			this.updateIndicator(lineL, lineW);

		},
		animationfinish(e) {

			let index = e.detail.current;
			if (this._touchTabIndex === index) {
				this.isTap = false;
			}
			this._lastTabIndex = index;
			this.switchTab(index);
			this.updateIndicator(this.tabListSize[index].left, this.tabListSize[index].width);

		},
		doTabAnimation() {},
		getTabbarItemsSize() {













			uni.createSelectorQuery().in(this).select('.tab-box').fields({
				dataset: true,
				size: true,
			}, (res) => {
				this.swiperWidth = res.width;
			}).exec();
			uni.createSelectorQuery().in(this).selectAll('.uni-tab-item').boundingClientRect((rects) => {
				rects.forEach((rect) => {
					this.tabListSize[rect.dataset.id] = rect;
				})
			}).exec();



			setTimeout(() => {
				this.updateIndicator(this.tabListSize[this.tabIndex].left, this.tabListSize[this.tabIndex].width);
			}, 100)

		},
		getElementSize(dom, ref, id) {
			dom.getComponentRect(ref, res => {
				this.tabListSize[id] = res.size;
			});
		},
		updateIndicator(left, width) {
			this.indicatorLineLeft = left;
			this.indicatorLineWidth = width;
		},
		switchTab(index) {
			console.log("更换界面")
			if (this.pageList[index].dataList.length === 0) {
				this.loadTabData(index);
			}

			if (this.tabIndex === index) {
				return;
			}

			// 缓存 tabId
			if (this.pageList[this.tabIndex].dataList.length > MAX_CACHE_DATA) {
				let isExist = this.cacheTab.indexOf(this.tabIndex);
				if (isExist < 0) {
					this.cacheTab.push(this.tabIndex);
				}
			}

			this.tabIndex = index;





			this.scrollInto = this.tabList[index].id;


			// 释放 tabId
			if (this.cacheTab.length > MAX_CACHE_PAGE) {
				let cacheIndex = this.cacheTab[0];
				this.clearTabData(cacheIndex);
				this.cacheTab.splice(0, 1);
			}
		},
		scrollTabTo(index) {
			console.log("滑块到？")
			const el = this.$refs['tabitem' + index][0];
			let offset = 0;
			// TODO fix ios offset
			if (index > 0) {
				offset = this.tabbarWidth / 2 - this.tabListSize[index].width / 2;
				if (this.tabListSize[index].right < this.tabbarWidth / 2) {
					offset = this.tabListSize[0].width;
				}
			}
			dom.scrollToElement(el, {
				offset: -offset
			});
		},
		loadTabData(index) {
			this.pageList[index].loadData();
		},
		clearTabData(index) {
			this.pageList[index].clear();
		}
	}
}
